@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}
img{
  width: 3cm;
  height: 3cm;
  align-self: center;
}

.home,
.reports,
.products {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.list{
  margin: 30%;
  margin-top: 1cm;
}
.list2{
  margin: 20%;
  margin-top: 1cm;

}
.card{
  text-align: center;
}

.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #1a83ff;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
  
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html {
  height: 100%;
}

body {
  min-height: 100%;
  font-family: "Titillium Web", "Source Sans Pro", Arial, sans-serif;
  font-size: 16px;
}

h1 {
  font-size: 1.5625rem;
}

h2 {
  font-size: 1.4375rem;
}

h3 {
  font-size: 1.3125rem;
}

h4 {
  font-size: 1.1875rem;
}

p {
  font-size: 1rem;
}


.overwatch-league {
  display: flex;
}

.left-content, .right-content {
  height: 80%;
  overflow: scroll;
  overflow-x: hidden;

  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}

.left-content::-webkit-scrollbar, .right-content::-webkit-scrollbar { 
  width: 0 !important;
}
.left-content::-webkit-scroll-bar-thumb, .right::-webkit-scrollbar-thumb {
  background: red;
}

.left-content {
  width: 30%;
  border-right: 2px solid #16181D;
}

.right-content {
  width: 80%;
}

.owl-teams, .team-information {
  margin-top: 3.5625rem;
}

.list-item {
  display: flex;
  align-items: center;
  padding: 1rem;
  border-bottom: 2px solid #16181D;
}

.list-item:hover {
  -webkit-filter: brightness(75%);
          filter: brightness(75%);
  cursor: pointer;
}

.list-item > .avatar {
  margin-right: 16px;
}

.list-item > .avatar > img {
  max-width: 48px;
}

.list-item > .information > h1 {
  font-weight: 300;
}

.team-name > .title {
  color: #000000;
  font-weight: 700;
  padding: 1rem 0;
}

.team-information > .subheading {
  padding: 1rem 0;
  width: 12.625rem;
  margin-bottom: 1.5rem;
  border-bottom: 5px solid #FA9C1E;
}

.player > .image > img {
  max-width: 13.24rem;
}

.media-content .title, .media-content .subtitle {
  color: #080808;
}
